.featured_blog {
  position: relative;
  overflow-x: scroll;
  .scroller-inner {
    position: relative;
    z-index: 2;
    flex-wrap: nowrap;
    align-items: flex-end;
    padding: 62px 16px;
    @media screena and (min-width: 768px) {
      padding: 24px;
    }
    &.amount-3 {
      min-width: calc(1314px + 748px)
      
    }
  }
}
.featured_blog-section {
  position: relative;
  overflow: hidden;
  margin-top: 40px;
  padding: 50px 0;
  padding-left: 24px;
}
.background_img {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  right: 0;
  bottom:  0;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.card {
  flex: 1 0 300px;
  width: 300px;
  max-height: 400px;
  position: relative;
  overflow: hidden;
  @media screena and (min-width: 768px) {
    flex-basis: 406px;
    width: 406px;
  }
  a {
    color: var(--color-dark);
  }
  &.main_card {
    flex: 1 0 500px;
    min-width: 500px;
    align-self: center;
    @media screena and (min-width: 768px) {
      flex-basis: 700px;
      min-width: 700px;
      height: 684px;
    }
    h2, div {
      max-width: 494px;
      margin: 0 auto;
      color: var(--color-dark);
    }
  }
  picture {
    display: block;
    position: relative;
    z-index: 1;
    img {
      display: block;
    }
  }
  header {
    position: absolute;
    z-index: 2;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 16px;
  }
  &:nth-child(3n+1) {
    header {
      background-color: rgba(var(--color-yellow-rgb), 0.8);
    }
  }
  &:nth-child(3n+2) {
    header {
      background-color: rgba(var(--color-pink-rgb), 0.8);
    }
  }
  &:nth-child(3n+3) {
    header {
      background-color: rgba(var(--color-red-rgb), 0.8);
    }
  }
}

.featured_blog-section .featured_blog {
  scrollbar-color: #666 #201c29;
  -webkit-overflow-scrolling: touch;
  display: block;
  overflow-x: scroll;
  position: relative;
  z-index: 2;
}

.featured_blog-section .featured_blog::-webkit-scrollbar {
  width: 2px;
  height: 2px;
  background-color: white; /* or add it to the track */
}

.featured_blog-section .featured_blog::-webkit-scrollbar-thumb {
  background: var(--color-red);
  border-radius: 1px;
}

.featured_blog-section .featured_blog::-webkit-scrollbar-track {
  background: white;
}
