.multicolumn.grid {
  grid-template-columns: repeat(6, minmax(0, 1fr));
  list-style-type: none;
  margin: 40px 0 0 0;
  padding: 0;
}
.column {
  position: relative;
  .image {
    position: relative;
    z-index: 1;
    height: 100%;
    width: 100%;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  .button {
    margin-top: 16px;
    &.bottom {
      
    }
  }
  
  h2, h3, h4, p, .buttons {
    flex: 1 1 100%;
  }
  grid-column: span 6;
  @media screen and (min-width: 768px) {
    &.half {
      grid-column: span 3;
    }
    &.one_third {
      grid-column: span 2;
    }
    &.two_thirds {
      grid-column: span 4;
    }
  }
}